import React, { Fragment, useState } from "react";
import proof from "../../styles/assets/images/PocDevlopment/proof.png";
import researchImg from "../../styles/assets/images/PocDevlopment/research.png";
import development from "../../styles/assets/images/PocDevlopment/development.png";

import forPocProofImg from "../../styles/assets/images/PocDevlopment/proof.jpg";
import forPocResearchImg from "../../styles/assets/images/PocDevlopment/research-con.webp";
import forPocDevelopmentImg from "../../styles/assets/images/PocDevlopment/developmentcon.jpg";

import { Col, Nav, Row, Tab } from "react-bootstrap";

const pocData = [
  {
    img: proof,
    title: "PROOF OF CONCEPT",
    description:
      " With years of experience working with a wide range ofclients and technologies, we are well-equipped to help you unlock the full potential of your POC and open source projects. Whether you're looking to create a new product or service, or simply need help optimizing an existing one, we are here to help.",
    eventKey: "poc",
  },
  {
    img: researchImg,
    title: "RESEARCH CONCEPT DEVELOPMENT",
    description:
      "Our POC and open source development services includeeverything from research and concept development, to prototyping and testing, to full-scale development and deployment. We work closely with you to understand your unique needs and goals, and then use our expertise to help you achieve them.",
    eventKey: "research-dev",
  },
  {
    img: development,
    title: "PROVEN DEVELOPMENT METHODS",
    description:
      "With our cutting-edge technologies and proven development methodologies, we can help you create truly innovative and flexible products and services that are sure to stand out in the marketplace. And with our commitment to open source development, you can be confident that your projects will be built on the latest and greatest technologies, without the need to spend a fortune on licensing fees.",
    eventKey: "dev-method",
  },
];

const forPocImg = [
  {
    img: forPocProofImg,
    alt: "poc-img",
    eventKey: "poc",
  },
  {
    img: forPocResearchImg,
    alt: "research-img",
    eventKey: "research-dev",
  },
  {
    img: forPocDevelopmentImg,
    alt: "development-img",
    eventKey: "dev-method",
  },
];

const DevelopmentServices = () => {
  // const [selectedIdx, setSelectedIdx] = useState(0);
  const [key, setKey] = useState("poc");

  // const handleMouseSelect = (ele, index) => {
  //   const idx = forPocImg?.findIndex((e, i) => i === index);
  //   console.log("idx::", idx);
  //   setSelectedIdx(idx);
  // };

  return (
    <Fragment>
      <div class="business-wrap my-5 pt-5">
        <div class="container-fluid">
          <div class="business-need p-5 row">
            <div class="col-12">
              <div class="mx-5">
                <h3 class="mb-2 fw-bold">
                  POC And Open Source Development Services
                </h3>
                <p class="text-dark fw-semibold fs-3 mt-4">
                  Looking for a reliable and innovative partner to help you
                  navigate the complex world of POC and open source development?
                  Look no further than our expert team at Justtry.
                </p>
              </div>
            </div>
          </div>
        </div>
      </div>
      <div class="container">
        <div class="text-center justify-content-center d-flex row">
          <div class="col-md-7">
            <h4 class="fw-semibold text-dark my-5">
              Unlock the Power of Innovation and Flexibility with Our Expertise
            </h4>
          </div>
        </div>
        <Tab.Container activeKey={key}>
          <Row className="d-flex align-items-center">
            <Col className="col-md-6 col-lg-6 col-sm-8 col-12 d-none d-md-block">
              <Tab.Content>
                {forPocImg.map((ele, ind) => (
                  <Tab.Pane key={ind} eventKey={ele?.eventKey}>
                    <div>
                      <img
                        className="d-flex m-auto d-flex justify-content-center shadow rounded w-75"
                        src={ele.img}
                        alt="poc"
                      />
                    </div>
                  </Tab.Pane>
                ))}
              </Tab.Content>
            </Col>
            <Col className="col-md-6 col-lg-6 col-sm-8 col-12">
              <Row>
                <Col md={12}>
                  <div className="webdev-head ">
                    <h2 className="text-capitalize titlecard fs-1 font-weight-normal">
                      POC and Open Source Development Services
                    </h2>
                  </div>
                </Col>
              </Row>
              <Row className="webdev-nav-link">
                <Col md={12}>
                  <Nav variant="pills" className="flex-column">
                    {pocData.map((ele, ind) => {
                      const { img, description, title, eventKey } = ele;
                      return (
                        <Nav.Item
                          key={ind}
                          onMouseEnter={() => setKey(eventKey)}
                        >
                          <Nav.Link eventKey={eventKey}>
                            <img alt="poc" src={img} />
                            {title}
                          </Nav.Link>
                          <p>{description}</p>
                        </Nav.Item>
                      );
                    })}
                  </Nav>
                </Col>
              </Row>
            </Col>
          </Row>
        </Tab.Container>
        <div class="row">
          <h3 class="titlecard fw-bold">Conclusion</h3>
          <p class="fs-3 fw-semibold">
            So why wait? Contact us today to learn more about our POC and open
            source development services, and to see how we can help you unleash
            the full power of innovation and flexibility in your next project.
          </p>
        </div>
      </div>
    </Fragment>
  );
};

export default DevelopmentServices;
